export function getProduct(product_code){
    //  establezco el formulario de datos de la peticin

let typeDelivery="2"
let token = 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpYXQiOjE2NTY0NTAxNDIsImV4cCI6MTY1NjUzNjU0MiwiZGF0YSI6eyJ1c2VySWQiOjAsImNhcnRJZCI6MzI0MTczMCwiaXAiOiIxODYuOTkuMTY1LjEwNiJ9fQ.JZnliL8svQdx4bPi59kGoSwyhSqLkTmaEwkA2EDtz-M'
let url = 'https://ferreteriaonlinehoyostools.com/admin/frontend/web/index.php/items/items/detalles-items-vr'

let formData = new FormData();
formData.append("codigos", product_code);
formData.append("tipoEntrega", typeDelivery);

//  Se realiza la consulta de los datos del producto
fetch(url, {
  method: 'POST',
  body: formData,
  headers: {
    'Authorization': 'Bearer ' + token
  }
})
  .then(res => res.json())
  .then(response => {

    // console.log('data producto', response);
    if (response.error == false) {

      let dataProductResponse = response.resultItems[0];
      
      if (dataProductResponse && dataProductResponse.item.nombre) {
        const ClickProductEvent = new CustomEvent('load_product', { 
            detail:{
              nombre: dataProductResponse.item.nombre,
              imagen: dataProductResponse.item.imagen
            } 
        });
        document.dispatchEvent(ClickProductEvent);
      }

      else{
        alert("error producto"); 
      }
    }
    else {
      alert("error") 
      // console.log(response)
    }
  })
  .catch(error => console.error('Error:', error));
}

